import React from 'react';
import { Link } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { 
  AcademicCapIcon, 
  MapPinIcon, 
  ClockIcon, 
  CurrencyDollarIcon,
  ArrowRightIcon,
  ChatBubbleLeftRightIcon,
  BookOpenIcon,
  ShieldCheckIcon,
  HeartIcon
} from '@heroicons/react/24/outline';
import { StarIcon as StarIconSolid } from '@heroicons/react/24/solid';

const Home = () => {
  const { user } = useAuth();

  const features = [
    {
      icon: AcademicCapIcon,
      title: 'Wykwalifikowani Nauczyciele',
      description: 'Sprawdzeni nauczyciele z dowiadczeniem i pozytywnymi recenzjami'
    },
    {
      icon: MapPinIcon,
      title: 'Elastyczna Lokalizacja',
      description: 'Korepetycje online lub stacjonarnie - wybierz co Ci pasuje'
    },
    {
      icon: ClockIcon,
      title: 'Dostpno 24/7',
      description: 'Przegldaj ogoszenia i kontaktuj si z nauczycielami o kadej porze'
    },
    {
      icon: CurrencyDollarIcon,
      title: 'Przejrzyste Ceny',
      description: 'Wszystkie ceny s jasno okrelone - bez ukrytych kosztw'
    },
    {
      icon: ChatBubbleLeftRightIcon,
      title: 'Bezporedni Kontakt',
      description: 'Komunikuj si bezporednio z nauczycielem bez porednikw'
    },
    {
      icon: ShieldCheckIcon,
      title: 'Bezpieczne Patnoci',
      description: 'Bezpieczne transakcje i ochrona Twoich danych'
    }
  ];

  const stats = [
    { number: '500+', label: 'Nauczycieli' },
    { number: '1000+', label: 'Uczniw' },
    { number: '50+', label: 'Przedmiotw' },
    { number: '4.8', label: 'rednia Ocena' }
  ];

  const testimonials = [
    {
      name: 'Anna Kowalska',
      role: 'Studentka Liceum',
      content: 'wietna platforma! Znalazam nauczyciela matematyki, ktry wytumaczy mi wszystko w prosty sposb. Polecam!',
      rating: 5,
      subject: 'Matematyka'
    },
    {
      name: 'Piotr Nowak',
      role: 'Student',
      content: 'Korepetycje z angielskiego online to strza w dziesitk. Nauczycielka jest bardzo cierpliwa i profesjonalna.',
      rating: 5,
      subject: 'Jzyk Angielski'
    },
    {
      name: 'Maria Winiewska',
      role: 'Rodzic',
      content: 'Moja crka poprawia oceny z fizyki dziki korepetycjom tutaj. Nauczyciel ma wietne podejcie do dzieci.',
      rating: 5,
      subject: 'Fizyka'
    }
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-purple-50">
      {/* Hero Section */}
      <section className="relative overflow-hidden">
        <div className="absolute inset-0 bg-gradient-to-r from-blue-600 to-purple-600 opacity-10"></div>
        <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-20">
          <div className="text-center">
            <h1 className="text-4xl md:text-6xl font-bold text-gray-900 mb-6">
              Znajd Idealnego{' '}
              <span className="text-transparent bg-clip-text bg-gradient-to-r from-blue-600 to-purple-600">
                Nauczyciela
              </span>
            </h1>
            <p className="text-xl md:text-2xl text-gray-600 mb-8 max-w-3xl mx-auto">
              Platforma korepetycji online i stacjonarnych. Pocz si z wykwalifikowanymi nauczycielami 
              i osignij swoje cele edukacyjne.
            </p>
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <Link
                to="/tutoring"
                className="inline-flex items-center px-8 py-4 bg-gradient-to-r from-blue-600 to-purple-600 text-white font-semibold rounded-xl hover:from-blue-700 hover:to-purple-700 transition-all duration-200 transform hover:scale-105 shadow-lg"
              >
                Przegldaj Ogoszenia
                <ArrowRightIcon className="ml-2 h-5 w-5" />
              </Link>
              <Link
                to="/otp-login"
                className="inline-flex items-center px-8 py-4 bg-white text-gray-700 font-semibold rounded-xl border-2 border-gray-200 hover:border-blue-300 hover:bg-blue-50 transition-all duration-200"
              >
                Zaloguj si
              </Link>
            </div>
          </div>
        </div>
      </section>

      {/* Stats Section */}
      <section className="py-16 bg-white">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid grid-cols-2 md:grid-cols-4 gap-8">
            {stats.map((stat, index) => (
              <div key={index} className="text-center">
                <div className="text-3xl md:text-4xl font-bold text-gray-900 mb-2">
                  {stat.number}
                </div>
                <div className="text-gray-600">{stat.label}</div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Features Section */}
      <section className="py-20 bg-gray-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-16">
            <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">
              Dlaczego Warto Wybra Nasz Platform?
            </h2>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              Oferujemy wszystko, czego potrzebujesz do skutecznej nauki i rozwoju
            </p>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {features.map((feature, index) => (
              <div key={index} className="bg-white rounded-2xl p-8 shadow-lg hover:shadow-xl transition-all duration-300 transform hover:-translate-y-2">
                <div className="w-16 h-16 bg-gradient-to-r from-blue-500 to-purple-500 rounded-xl flex items-center justify-center mb-6">
                  <feature.icon className="h-8 w-8 text-white" />
                </div>
                <h3 className="text-xl font-semibold text-gray-900 mb-4">{feature.title}</h3>
                <p className="text-gray-600">{feature.description}</p>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Testimonials Section */}
      <section className="py-20 bg-gradient-to-r from-blue-50 to-purple-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-16">
            <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">
              Co Mwi Nasi Uytkownicy?
            </h2>
            <p className="text-xl text-gray-600">
              Opinie uczniw i rodzicw o naszej platformie
            </p>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            {testimonials.map((testimonial, index) => (
              <div key={index} className="bg-white rounded-2xl p-8 shadow-lg">
                <div className="flex items-center mb-4">
                  {[...Array(testimonial.rating)].map((_, i) => (
                    <StarIconSolid key={i} className="h-5 w-5 text-yellow-400" />
                  ))}
                </div>
                <p className="text-gray-600 mb-6 italic">"{testimonial.content}"</p>
                <div className="flex items-center justify-between">
                  <div>
                    <h4 className="font-semibold text-gray-900">{testimonial.name}</h4>
                    <p className="text-sm text-gray-600">{testimonial.role}</p>
                  </div>
                  <div className="text-sm text-blue-600 font-medium">
                    {testimonial.subject}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="py-20 bg-gradient-to-r from-blue-600 to-purple-600">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
          <h2 className="text-3xl md:text-4xl font-bold text-white mb-6">
            Gotowy na Rozpoczcie Nauki?
          </h2>
          <p className="text-xl text-blue-100 mb-8">
            Docz do tysicy zadowolonych uczniw i znajd swojego idealnego nauczyciela ju dzi!
          </p>
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <Link
              to="/tutoring"
              className="inline-flex items-center px-8 py-4 bg-white text-blue-600 font-semibold rounded-xl hover:bg-gray-100 transition-all duration-200 transform hover:scale-105"
            >
              <BookOpenIcon className="mr-2 h-5 w-5" />
              Przegldaj Ogoszenia
            </Link>
            <Link
              to="/otp-login"
              className="inline-flex items-center px-8 py-4 bg-transparent text-white font-semibold rounded-xl border-2 border-white hover:bg-white hover:text-blue-600 transition-all duration-200"
            >
              <HeartIcon className="mr-2 h-5 w-5" />
              Docz Teraz
            </Link>
          </div>
        </div>
      </section>

      {/* Footer */}
      <footer className="bg-gray-900 text-white py-12">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid grid-cols-1 md:grid-cols-4 gap-8">
            <div>
              <h3 className="text-xl font-bold mb-4">Platforma Korepetycji</h3>
              <p className="text-gray-400">
                czymy uczniw z najlepszymi nauczycielami. Twoja edukacja, nasza pasja.
              </p>
            </div>
            <div>
              <h4 className="font-semibold mb-4">Dla Uczniw</h4>
              <ul className="space-y-2 text-gray-400">
                <li><Link to="/tutoring" className="hover:text-white transition-colors">Przegldaj Ogoszenia</Link></li>
                <li><Link to="/favorites" className="hover:text-white transition-colors">Ulubione</Link></li>
                <li><Link to="/messages" className="hover:text-white transition-colors">Wiadomoci</Link></li>
              </ul>
            </div>
            <div>
              <h4 className="font-semibold mb-4">Dla Nauczycieli</h4>
              <ul className="space-y-2 text-gray-400">
                <li><Link to="/tutoring/create" className="hover:text-white transition-colors">Dodaj Ogoszenie</Link></li>
                <li><Link to="/dashboard" className="hover:text-white transition-colors">Panel Nauczyciela</Link></li>
                <li><Link to="/applications" className="hover:text-white transition-colors">Aplikacje</Link></li>
              </ul>
            </div>
            <div>
              <h4 className="font-semibold mb-4">Pomoc</h4>
              <ul className="space-y-2 text-gray-400">
                <li><Link to="/otp-info" className="hover:text-white transition-colors">Jak to dziaa</Link></li>
                <li><Link to="/otp-test" className="hover:text-white transition-colors">Test OTP</Link></li>
                <li><Link to="/profile" className="hover:text-white transition-colors">Profil</Link></li>
              </ul>
            </div>
          </div>
          <div className="border-t border-gray-800 mt-8 pt-8 text-center text-gray-400">
            <p>&copy; 2024 Platforma Korepetycji. Wszystkie prawa zastrzeone.</p>
          </div>
        </div>
      </footer>
    </div>
  );
};

export default Home; 